.products-container {
    width: 100%;
    padding: 1rem;
  
    .header {
      display: flex;
      margin-bottom: 1rem;

      h2 {
        font-size: 2rem;
      }

      button {
        background: white;
        border: 1px solid black;
        height: fit-content;
        padding: 0.3rem 0.7rem;
        font-size: 1.25rem;
        cursor: pointer;
        margin-left: auto;
  
        &:hover {
          background-color: black;
          color: white;
        }
      }
    }
  
    .product-list {
      display: flex;
      flex-direction: column;
      max-height: 22.9rem;
      overflow-y: scroll;
      gap: 0.6rem;
    }
  
    .product-card {
      display: flex;
      align-items: center;
      justify-content: space-between;
      border: 1px dashed #ccc;
      padding: 0.75rem;
      gap: 1rem;
  
      img {
        width: 6rem;
        height: 6rem;
        object-fit: cover;
        border-radius: 0.5rem;
      }
  
      .details {
        flex: 1;
  
        h3 {
          font-size: 1.25rem;
        }
  
        p, small {
          font-size: 0.875rem;
        }
      }
  
      .actions {
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
  
        button {
          background: transparent;
          border: none;
          cursor: pointer;
          font-size: 1.25rem;
  
          &:hover {
            scale: 1.1;
          }
        }
      }
    }
  
    .modal {
      position: fixed;
      top: 0; left: 0;
      width: 100vw;
      height: 100vh;
      background: rgba(0,0,0,0.4);
      display: flex;
      justify-content: center;
      align-items: center;
  
      .modal-content {
        background: white;
        padding: 2rem;
        border-radius: 0.5rem;
        display: flex;
        flex-direction: column;
        gap: 0.75rem;
        width: 90%;
        max-width: 30rem;
  
        input {
          padding: 0.5rem;
          font-size: 1rem;
          border: 1px solid #ccc;
        }

  
        button {
          padding: 0.5rem;
          border: none;
          font-size: 1rem;
          cursor: pointer;
  
          &:first-of-type {
            background-color: black;
            color: white;
          }
  
          &:last-of-type {
            background-color: #eee;
          }
        }
      }
    }
  }
  